<template>
  <div class="other">
    <top-nav></top-nav>
    
    <div class="mdui-card" >

      <!-- 卡片头部，包含头像、标题、副标题 -->
      <div class="mdui-card-header" >
        <img class="mdui-card-header-avatar"  :src="tableData[0].url" :title="tableData[0].url"/>
        <!-- <a>{{tableData[0].url}}+1</a> -->
        <div class="mdui-card-header-title">公告</div>
        <div class="mdui-card-header-subtitle">发布：潦草爱读书</div>
      </div>

      <!-- 卡片的媒体内容，可以包含图片、视频等媒体内容，以及标题、副标题 -->
      <div class="mdui-card-media">
        <img src="../assets/img/2.webp"  title="hello"/>

        <!-- 卡片中可以包含一个或多个菜单按钮 -->
        <div class="mdui-card-menu">
          <button class="mdui-btn mdui-btn-icon mdui-text-color-white">
            <!-- <i class="mdui-icon material-icons">share</i> -->
          </button>
        </div>
      </div>

      <!-- 卡片的标题和副标题 -->
      <div class="mdui-card-primary">
        <div class="mdui-card-primary-title">杭州线下2048大比拼</div>
        <div class="mdui-card-primary-subtitle">都来！</div>
      </div>

      <!-- 卡片的内容 -->
      <div class="mdui-card-content">“现在谁还玩2048阿！”</div>

      <div class="form-signin-heading" id="btnUpload" @change="upload">上传图片</div>
      <input type="file" name="avatar" id="avatar" multiple="multiple" @change="upload">
      
       <!-- 图片上传 -->
      <el-form-item label="首图上传" style="width:100px;height: 100px;">
            <el-upload
              action="../../dist"
              list-type="picture-card"
              :on-success="handleAvatarSuccess"
              
              name="img"           
              
            >
            
            <img v-if="imageUrl" :src="imageUrl" class="avatar" alt="1" style="width:100px;height: 100px;"/>
            <img v-else src="../assets/img/bqb1.jpg" alt="2" style="width:100px;height: 100px;">
            </el-upload>
      </el-form-item>



      <!-- <button class="mdui-card-actions" style="color:grey;" @click="this.$router.push('/otherinfo');">点击查收公告</button> -->

      <br>
      <br>
      <!-- <br>
      <br>
      <br>
      <br>
      <br> -->
      

    </div>


    <br>
    <br>
    <br>
    <br>
    <bottom-view class="bottom"></bottom-view>
  </div>
</template>

<script>
import topNav from '@/components/topNav.vue'
// import SwiperCom from '@/components/swiperCom.vue'
import bottomView from '../components/bottomView.vue'
import axios from 'axios'

import {  reactive, toRefs } from 'vue'

export default {
  name: 'OtherView',
  components: {
    topNav,
    bottomView
    // SwiperCom
  },
  data() {
      return {
        tableData: [{
            // id:'',
            // date_time: '',
            // integral_number: '',
            // amount: '',
            photos_url: '123'
        }],
        imageUrl: ''
      }
    },

    handleAvatarSuccess(res) {
      //res这个参数自己打印出来一看便知，在此不做解释     
      console.log(res);
     //将后端发送的地址赋值到我们需要显示的img中的src动态绑定的参数中
      this.imageUrl = res.data.path;
     //将图片地址绑定到我们的form表单数据中 后期存入数据库中     
      this.tableData.photos_url = res.data.path;
    },
    beforeAvatarUpload(file) {
           const isJPG = file.type === "image/jpeg";
           const isPNG = file.type === "image/png";
           const isLt3M = file.size / 1024 / 1024 / 1024 < 3;
           if (!isJPG && !isPNG) {
             this.$message.error`在这里插入代码片`("上传头像图片只能是 JPG 格式或 png 格式!");
           }
           if (!isLt3M) {
             this.$message.error("上传头像图片大小不能超过 3MB!");
           }
           return isJPG && isLt3M;
         },

    mounted() {
        this.all()//每次进入该页面自动执行该方法，即自动读取数据库数据导入到页面当中
        var upload = document.getElementById("btnUpload");
        var avatar = document.getElementById("avatar");
        upload.onclick =function(){
          avatar.click(); //注意IE的兼容性
        };
    },

    methods: {
        all(){
       axios.get('http://localhost:3001/select_success').then(res=>{
         this.tableData=res.data;//数据传递到页面数组
         console.log("数据查询成功"+res.data)
         console.log(res.data)
         
       }).catch(err=>{
         console.log("获取数据失败"+err);
       })
     }
    
    },

//     mounted: function () {
//       var upload = document.getElementById("btnUpload");
//       var avatar = document.getElementById("avatar");
//       upload.onclick =function(){
//         avatar.click(); //注意IE的兼容性
//       };
//     },
    upload: function (e) {
        var that = this;
        let formData = new window.FormData();
        let file = e.target.files[0];
        formData.append('file',file);//通过append向form对象添加数据
        //利用split切割，拿到上传文件的格式
        var src = file.name,
          formart = src.split(".")[1];
        //使用if判断上传文件格式是否符合
        if (formart == "jpg" || formart == "png" ||
          formart == "docx" || formart == "txt" ||
          formart == "ppt" || formart == "xlsx" ||
          formart == "zip" || formart == "rar" ||
          formart == "doc") {
          //只有满足以上格式时，才会触发ajax请求
          this.$axios.post(this.$api.personalCenter.upFile,formData).then(function (res) {
            that.upFileData = res.data;
          }).then(function (res) {
            var params = {
              photos_url: that.upFileData,
              // photo_des: ''
            };
//            console.log(params.photos_url,'photos_url')
            that.$axios.post(that.$api.personalCenter.wallAdd,res.stringify(params)).then(function (res) {
              console.log(res.data);
              that.$options.methods.imgList.bind(that)();
            }).catch(function (err) {
              console.log(err);
              console.log("请求出错");
            })
          })
        } else {
          alert("文件格式不支持上传");
        }
      },
  setup(){
        const state = reactive({
          urls: [{
            photos_url: ''
          }]
        })
    // console.log(this.tableData)
    
    return {
      ...toRefs(state),
      
    }

  }
}


</script>

<style scoped>
.other{
    text-align: center;
    background: #faf8ef;
    
}
    .swiper{
        width: 200px;
        border: 1px solid rgb(238, 235, 235);
        
        padding: 1px;
        border-radius: 15px;
        background: white;
    }

.mdui-card{
  background: #faf8ef;
  margin: 24px;
  padding:8px;
  /* width: 300px; */
  /* height: 500px; */
  text-align: center;
  border: 1px solid #dad8d1;
}

.ch{
  letter-spacing: 2px;
}

.bottom{
  position: fixed;
  bottom: 0;
  width: 100%;
}
</style>
